    </div>

    <!-- QR ÏΩîÎìú Ïù∏Ïãù ÌõÑ ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ ÏûÖÎ†• Î™®Îã¨ -->
    <div v-if="showQrUserNameModal" class="qr-username-modal">
      <div class="qr-username-content">
        <h3>ÌôòÏòÅÌï©ÎãàÎã§!</h3>
        <p>QR ÏΩîÎìúÎ•º ÌÜµÌï¥ Î∞©Ïóê Ï∞∏Ïó¨ÌïòÏÖ®ÏäµÎãàÎã§. ÏÇ¨Ïö©Ïûê Ïù¥Î¶ÑÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.</p>
        <input
          v-model="qrUserName"
