                </div>
                <div v-else>
                  <div class="drop-icon">üì§</div>
                  <p>ÌååÏùºÏùÑ ÎìúÎûòÍ∑∏ÌïòÍ±∞ÎÇò ÌÅ¥Î¶≠ÌïòÏÑ∏Ïöî</p>
                  <p style="font-size: 12px; color: #666; margin-top: 5px;">ÏûêÎèôÏúºÎ°ú ÏóÖÎ°úÎìúÎê©ÎãàÎã§</p>
                </div>
                <input
