              <span v-if="isDownloadingAll" class="loading-spinner"></span>
            </button>
          </div>

          <div v-if="isLoading" class="loading-message">
            ÌååÏùº Î™©Î°ùÏùÑ Î∂àÎü¨Ïò§Îäî Ï§ë...
          </div>
          <div v-else-if="files.length === 0" class="empty-message">
            Í≥µÏú†Îêú ÌååÏùºÏù¥ ÏóÜÏäµÎãàÎã§.
          </div>
          <ul v-else class="file-list">
            <li
              v-for="file in files"
              :key="file._uniqueKey || generateUUID()"
              class="file-item"
              :class="{
                'is-office-document': file.isOfficeDocument,
                'is-selected': selectedDocument && selectedDocument.filename === file.filename
              }"
            >
              <div class="file-info">
                <div class="file-header">
                  <span class="file-name">{{ file.originalName }}</span>
                  <span class="file-uploader">
                    <span class="uploader-label">ÏóÖÎ°úÎçî:</span>
                    <span class="uploader-name" :class="{ 'my-upload': file.userName === userName }">
                      {{ file.userName || 'ÏùµÎ™Ö' }}
                      <span v-if="file.userName === userName" class="my-upload-badge">(ÎÇ¥ ÌååÏùº)</span>
                    </span>
                  </span>
                </div>
                <div class="file-details">
                  <span class="file-size">{{ formatFileSize(file.size) }}</span>
                  <span class="file-date">{{ formatDate(file.uploadedAt) }}</span>
                  <!-- ÎßåÎ£å ÏãúÍ∞Ñ ÌëúÏãú -->
                  <span v-if="file.expiresAt" class="file-expiry" :class="{ 'expiry-soon': isExpiringSoon(file) }">
                    ÎßåÎ£å: {{ formatExpiryTime(file.expiresAt) }}
                  </span>
                  <!-- ÎîîÎ≤ÑÍπÖÏö© -->
                  <span class="debug-info" style="display: none;">{{ JSON.stringify(file) }}</span>
                  <span v-if="file.viewCount > 0" class="view-count">{{ file.viewCount }} Î™Ö Î≥¥Îäî Ï§ë</span>
                </div>
              </div>
              <div class="file-actions">
                <button @click="previewFile(file)" class="preview-button">
                  ÎØ∏Î¶¨Î≥¥Í∏∞
                </button>
                <button @click="downloadFile(file)" class="download-button">
                  Îã§Ïö¥Î°úÎìú
                </button>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <!-- QR ÏΩîÎìú Î™®Îã¨ -->
    <div class="qr-code-modal" v-if="showQrCodeModal" @click="closeQrCodeModal">
      <div class="qr-code-modal-content" @click.stop>
        <div class="qr-code-modal-header">
          <h3>QR ÏΩîÎìú</h3>
          <button class="close-button" @click="closeQrCodeModal">√ó</button>
        </div>
        <div class="qr-code-modal-body">
          <div class="qr-code-large" ref="qrcodeLarge"></div>
          <p class="qr-code-modal-text">Ïù¥ QR ÏΩîÎìúÎ•º Ïä§Ï∫îÌïòÏó¨ Î∞©Ïóê Ï∞∏Ïó¨ÌïòÏÑ∏Ïöî</p>
          <p class="qr-code-modal-room">Î∞© ÏΩîÎìú: {{ roomCode }}</p>
        </div>
      </div>
    </div>

    <!-- Ïó∞Í≤∞ ÏÉÅÌÉú ÌëúÏãú Î∞∞ÎÑà -->
    <div v-if="!isSocketConnected" class="connection-status-banner">
      <div class="connection-status-icon">
        <span class="connection-dot"></span>
      </div>
      <div class="connection-status-text">
        ÏÑúÎ≤ÑÏôÄÏùò Ïó∞Í≤∞Ïù¥ ÎÅäÍ≤ºÏäµÎãàÎã§. Ïû¨Ïó∞Í≤∞ Ï§ë...
      </div>
    </div>

    <!-- ÌååÏùº ÎØ∏Î¶¨Î≥¥Í∏∞ Î™®Îã¨ -->
    <div class="preview-modal" v-if="previewModal.show" @click="closePreview">
      <div class="preview-content" @click.stop>
        <div class="preview-header">
          <h3>{{ previewModal.file ? previewModal.file.originalName : 'ÌååÏùº ÎØ∏Î¶¨Î≥¥Í∏∞' }}</h3>
          <button class="close-button" @click="closePreview">√ó</button>
        </div>
        <div class="preview-body">
          <div v-if="previewModal.isLoading" class="preview-loading">
            <div class="loading-spinner"></div>
            <p>ÎØ∏Î¶¨Î≥¥Í∏∞ Î°úÎî© Ï§ë...</p>
          </div>

          <!-- Ïù¥ÎØ∏ÏßÄ ÎØ∏Î¶¨Î≥¥Í∏∞ -->
          <img
            v-else-if="previewModal.file && isImageFile(previewModal.file)"
            :src="previewModal.previewUrl"
            alt="Ïù¥ÎØ∏ÏßÄ ÎØ∏Î¶¨Î≥¥Í∏∞"
            class="preview-image"
          />

          <!-- PDF ÎØ∏Î¶¨Î≥¥Í∏∞ -->
          <iframe
            v-else-if="previewModal.file && isPdfFile(previewModal.file)"
            :src="previewModal.previewUrl"
            class="preview-pdf"
            frameborder="0"
          ></iframe>

          <!-- Excel ÌååÏùº ÎØ∏Î¶¨Î≥¥Í∏∞ -->
          <iframe
            v-else-if="previewModal.file && isExcelFile(previewModal.file)"
            :src="previewModal.officeViewerUrl"
            class="preview-excel"
            frameborder="0"
          ></iframe>

          <!-- Office ÌååÏùº ÎØ∏Î¶¨Î≥¥Í∏∞ -->
          <iframe
            v-else-if="previewModal.file && isOfficeFile(previewModal.file)"
            :src="previewModal.officeViewerUrl"
            class="preview-office"
            frameborder="0"
          ></iframe>

          <!-- ÎπÑÎîîÏò§ ÎØ∏Î¶¨Î≥¥Í∏∞ -->
          <video
            v-else-if="previewModal.file && isVideoFile(previewModal.file)"
            :src="previewModal.previewUrl"
            controls
            class="preview-video"
          ></video>

          <!-- Ïò§ÎîîÏò§ ÎØ∏Î¶¨Î≥¥Í∏∞ -->
          <audio
            v-else-if="previewModal.file && isAudioFile(previewModal.file)"
            :src="previewModal.previewUrl"
            controls
            class="preview-audio"
          ></audio>

          <!-- ÌÖçÏä§Ìä∏ ÎØ∏Î¶¨Î≥¥Í∏∞ -->
          <div
            v-else-if="previewModal.file && isTextFile(previewModal.file) && previewModal.textContent"
            class="preview-text"
          >
            <pre>{{ previewModal.textContent }}</pre>
          </div>

          <!-- ÎØ∏Î¶¨Î≥¥Í∏∞ Î∂àÍ∞ÄÎä•Ìïú ÌååÏùº -->
          <div v-else class="preview-unsupported">
            <div class="unsupported-icon">üìÑ</div>
            <p>Ïù¥ ÌååÏùº ÌòïÏãùÏùÄ ÎØ∏Î¶¨Î≥¥Í∏∞Î•º ÏßÄÏõêÌïòÏßÄ ÏïäÏäµÎãàÎã§.</p>
            <button @click="downloadFile(previewModal.file)" class="download-button">
              Îã§Ïö¥Î°úÎìú
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import io from 'socket.io-client'

// UUID ÏÉùÏÑ± Ìï®Ïàò
function generateUUID() {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
    const r = Math.random() * 16 | 0
    const v = c === 'x' ? r : (r & 0x3 | 0x8)
    return v.toString(16)
  })
}

export default {
  name: 'RoomView',
  data() {
    const apiBaseUrl = process.env.VUE_APP_API_URL;

    return {
      roomCode: this.$route.params.roomId,
      expiresIn: 3600,
      timerInterval: null,
      localTimerInterval: null,
      apiUrl: apiBaseUrl,
      socketUrl: apiBaseUrl,
      socket: null,
      isSocketConnected: false,
      pingInterval: null,
      lastPongTime: null,
      selectedFile: null,
      isUploading: false,
      uploadProgress: 0,
      uploadError: '',
      uploadSuccess: '',
      files: [],
      isLoading: false,
      userId: `user_${Date.now()}_${Math.floor(Math.random() * 1000)}`, // ÏûÑÏãú ÏÇ¨Ïö©Ïûê ID
      hasJoinedRoom: false, // Î∞© Ï∞∏Ïó¨ Ïó¨Î∂Ä Ï∂îÏ†Å
      isDragging: false, // ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ ÏÉÅÌÉú Ï∂îÏ†Å
      isDownloadingAll: false, // Ï†ÑÏ≤¥ ÌååÏùº Îã§Ïö¥Î°úÎìú ÏÉÅÌÉú
      notification: {
        show: false,
        message: '',
        timeout: null,
        isError: false
      },
      userName: '', // ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ Ï†ÄÏû• Î≥ÄÏàò Ï∂îÍ∞Ä
      isEditingUserName: false, // ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ Ìé∏Ïßë Î™®Îìú ÏÉÅÌÉú
      editUserName: '', // Ìé∏Ïßë Ï§ëÏù∏ ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ
      previewModal: {
        show: false,
        file: null,
        previewUrl: '',
        officeViewerUrl: '',
        textContent: null,
        isLoading: false
      },
      // QR ÏΩîÎìú Í¥ÄÎ†® Î≥ÄÏàò
      qrCodeUrl: '',
      showQrUserNameModal: false,
      showQrCodeModal: false,
      qrUserName: '',
      isQrScanned: false
    }
  },
  async created() {
    // ÌôòÍ≤Ω Î≥ÄÏàò ÌôïÏù∏ Î∞è Î°úÍπÖ
    console.log('API URL:', this.apiUrl)
    console.log('Socket URL:', this.socketUrl)

    // URL ÏøºÎ¶¨ ÌååÎùºÎØ∏ÌÑ∞ÏóêÏÑú userName Í∞ÄÏ†∏Ïò§Í∏∞
    console.log('URL ÏøºÎ¶¨ ÌååÎùºÎØ∏ÌÑ∞:', this.$route.query);

    // URLÏóêÏÑú ÏßÅÏ†ë userName ÌååÎùºÎØ∏ÌÑ∞ Ï∂îÏ∂ú
    const urlParams = new URLSearchParams(window.location.search);
    const userNameFromUrl = urlParams.get('userName');
    console.log('URLÏóêÏÑú ÏßÅÏ†ë Ï∂îÏ∂úÌïú userName:', userNameFromUrl);

    // QR ÏΩîÎìú Ïä§Ï∫î Ïó¨Î∂Ä ÌôïÏù∏
    const fromQr = urlParams.get('fromQr') === 'true';

    if (fromQr && !this.isQrScanned) {
      // QR ÏΩîÎìúÎ•º ÌÜµÌï¥ Ï†ëÏÜçÌïú Í≤ΩÏö∞ ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ ÏûÖÎ†• Î™®Îã¨ ÌëúÏãú
      this.isQrScanned = true;
      this.showQrUserNameModal = true;
      // Í∏∞Î≥∏ ÏÇ¨Ïö©Ïûê Ïù¥Î¶ÑÏùÄ Îπà Í∞íÏúºÎ°ú ÏÑ§Ï†ï
      this.qrUserName = '';
      this.userName = '';
    } else {
      // ÏùºÎ∞ò Ï†ëÏÜçÏù∏ Í≤ΩÏö∞ URLÏóêÏÑú ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ Í∞ÄÏ†∏Ïò§Í∏∞ (Í∏∞Î≥∏Í∞í: 'ÎÇ¥ ÌååÏùº')
      this.userName = userNameFromUrl || 'ÎÇ¥ ÌååÏùº';
      console.log('ÏµúÏ¢Ö ÏÑ§Ï†ïÎêú ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ:', this.userName);

      // ÌòÑÏû¨ URLÏóê userName ÌååÎùºÎØ∏ÌÑ∞Í∞Ä ÏóÜÏúºÎ©¥ Ï∂îÍ∞Ä
      if (!userNameFromUrl) {
        const currentUrl = new URL(window.location.href);
        currentUrl.searchParams.set('userName', this.userName);
        window.history.replaceState({}, '', currentUrl.toString());
      }
    }

    try {
      // Î∞© Ï∞∏Ïó¨ ÎòêÎäî ÏÉùÏÑ± ÏãúÎèÑ
      await this.joinOrCreateRoom()
      this.startExpiryTimer()
      this.loadFiles()
      this.connectSocket()

      // QR ÏΩîÎìú URL ÏÉùÏÑ± (Î™®Îã¨ÏóêÏÑú ÏÇ¨Ïö©Ìï† URL ÎØ∏Î¶¨ Ï§ÄÎπÑ)
      this.generateQrCode()

      // QR ÏΩîÎìú Ïä§Ï∫î ÌõÑ ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ ÏûÖÎ†• Î™®Îã¨Ïù¥ ÌëúÏãúÎêú Í≤ΩÏö∞ ÏûÖÎ†• ÌïÑÎìúÏóê Ìè¨Ïª§Ïä§
      if (this.showQrUserNameModal) {
        this.$nextTick(() => {
          if (this.$refs.qrUserNameInput) {
            this.$refs.qrUserNameInput.focus();
          }
        });
      }
    } catch (error) {
      console.error('Î∞© Ï∞∏Ïó¨ Ïò§Î•ò:', error)
      this.navigateToHome()
    }
  },
  beforeDestroy() {
    // ÌÉÄÏù¥Î®∏ Ï†ïÎ¶¨
    if (this.timerInterval) {
      clearInterval(this.timerInterval);
    }

    if (this.localTimerInterval) {
      clearInterval(this.localTimerInterval);
    }

    // Ìïë ÌÉÄÏù¥Î®∏ Ï†ïÎ¶¨
    if (this.pingInterval) {
      clearInterval(this.pingInterval);
      this.pingInterval = null;
    }

    // Socket.IO Ïó∞Í≤∞ Ìï¥Ï†ú
    if (this.socket) {
      // Ïó∞Í≤∞ Ìï¥Ï†ú Ï†ÑÏóê Î°úÍ∑∏ Ï∂úÎ†•
      console.log('Ïª¥Ìè¨ÎÑåÌä∏ ÏÜåÎ©∏ Ïãú Socket.IO Ïó∞Í≤∞ Ìï¥Ï†ú');

      // Î™ÖÏãúÏ†ÅÏúºÎ°ú Ïó∞Í≤∞ Ìï¥Ï†ú
      this.socket.disconnect();
      this.socket = null;
    }
  },
  methods: {
    async joinOrCreateRoom() {
      try {
        // Î∞© Ï∞∏Ïó¨ ÏãúÎèÑ
        const response = await axios.post(`${this.apiUrl}/api/rooms/${this.roomCode}`, {}, 
          { withCredentials: true }
        )
        this.expiresIn = response.data.expiresIn || 3600

        // Ï≤òÏùå Ï∞∏Ïó¨ÌïòÎäî Í≤ΩÏö∞ÏóêÎßå ÏïåÎ¶º ÌëúÏãú
        if (!this.hasJoinedRoom) {
          this.showNotification('Î∞©Ïóê ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Ï∞∏Ïó¨ÌñàÏäµÎãàÎã§.')
          this.hasJoinedRoom = true
        }
      } catch (error) {
        if (error.response && error.response.status === 404) {
          // Î∞©Ïù¥ ÏóÜÎäî Í≤ΩÏö∞, 6ÏûêÎ¶¨ Ïà´ÏûêÏù∏ÏßÄ ÌôïÏù∏
          if (/^\d{6}$/.test(this.roomCode)) {
            // Î∞© ÏÉùÏÑ± ÏãúÎèÑ
            try {
              const createResponse = await axios.post(`${this.apiUrl}/api/rooms`, { roomCode: this.roomCode }, {
                withCredentials: true
              })
              this.expiresIn = createResponse.data.expiresIn || 3600
              this.showNotification('ÏÉà Î∞©Ïù¥ ÏÉùÏÑ±ÎêòÏóàÏäµÎãàÎã§.')
              this.hasJoinedRoom = true
            } catch (createError) {
              console.error('Î∞© ÏÉùÏÑ± Ïò§Î•ò:', createError)
              throw createError
            }
          } else {
            throw error
          }
        } else {
          throw error
        }
      }
    },

    async checkRoomStatus() {
      try {
        const response = await axios.get(`${this.apiUrl}/api/rooms/${this.roomCode}/status`, {
          withCredentials: true
        })
        if (!response.data.active) {
          this.navigateToHome()
          alert('Î∞©Ïù¥ ÎßåÎ£åÎêòÏóàÏäµÎãàÎã§.')
          return
        }

        // ÏÑúÎ≤ÑÏóêÏÑú Î∞õÏùÄ ÎßåÎ£å ÏãúÍ∞ÑÏúºÎ°ú ÏóÖÎç∞Ïù¥Ìä∏
        const serverExpiresIn = response.data.expiresIn

        // ÎßåÎ£å ÏãúÍ∞ÑÏù¥ Î≥ÄÍ≤ΩÎêú Í≤ΩÏö∞ÏóêÎßå ÏóÖÎç∞Ïù¥Ìä∏
        if (this.expiresIn !== serverExpiresIn) {
          console.log('ÏÑúÎ≤Ñ ÎßåÎ£å ÏãúÍ∞ÑÏúºÎ°ú ÏóÖÎç∞Ïù¥Ìä∏:', serverExpiresIn)
          this.expiresIn = serverExpiresIn

          // Î°úÏª¨ ÌÉÄÏù¥Î®∏ Ïû¨ÏãúÏûë
          this.updateTimer()
        }
      } catch (error) {
        console.error('Î∞© ÏÉÅÌÉú ÌôïÏù∏ Ïã§Ìå®:', error)
        this.navigateToHome()
      }
    },
    navigateToHome() {
      // Ï§ëÎ≥µ Ïù¥Îèô Ïò§Î•ò Ï≤òÎ¶¨
      this.$router.push('/').catch(err => {
        if (err.name !== 'NavigationDuplicated') {
          throw err
        }
      })
    },
    startExpiryTimer() {
      // Ï¥àÍ∏∞ ÌÉÄÏù¥Î®∏ ÏãúÏûë
      this.updateTimer()

      // 5Ï¥àÎßàÎã§ ÏÑúÎ≤ÑÏóêÏÑú Ïã§Ï†ú ÎßåÎ£å ÏãúÍ∞Ñ ÌôïÏù∏
      this.timerInterval = setInterval(() => {
        this.checkRoomStatus()
      }, 5000)
    },

    updateTimer() {
      // Î°úÏª¨ ÌÉÄÏù¥Î®∏ (ÏÑúÎ≤Ñ ÏóÖÎç∞Ïù¥Ìä∏ ÏÇ¨Ïù¥Ïóê ÏÇ¨Ïö©)
      if (this.localTimerInterval) {
        clearInterval(this.localTimerInterval)
      }

      this.localTimerInterval = setInterval(() => {
        this.expiresIn--
        if (this.expiresIn <= 0) {
          clearInterval(this.localTimerInterval)
          clearInterval(this.timerInterval)
          this.navigateToHome()
          alert('Î∞©Ïù¥ ÎßåÎ£åÎêòÏóàÏäµÎãàÎã§.')
        }
      }, 1000)
    },
    formatTime(seconds) {
      const hours = Math.floor(seconds / 3600)
      const minutes = Math.floor((seconds % 3600) / 60)
      const remainingSeconds = seconds % 60

      return `${hours}:${String(minutes).padStart(2, '0')}:${String(remainingSeconds).padStart(2, '0')}`
    },

    // ÌååÏùº ÏóÖÎ°úÎìú Í¥ÄÎ†® Î©îÏÑúÎìú
    handleFileChange(event) {
      this.selectedFile = event.target.files[0] || null
      this.uploadError = ''
      this.uploadSuccess = ''

      // ÌååÏùºÏù¥ ÏÑ†ÌÉùÎêòÎ©¥ ÏûêÎèôÏúºÎ°ú ÏóÖÎ°úÎìú ÏãúÏûë (Î™®Î∞îÏùº ÏÇ¨Ïö©Ïûê Ìé∏ÏùòÏÑ± Í∞úÏÑ†)
      if (this.selectedFile) {
        this.showNotification('ÌååÏùº ÏóÖÎ°úÎìú Ï§ë...', 10000) // ÏóÖÎ°úÎìú Ï§ë ÏïåÎ¶º (ÏµúÎåÄ 10Ï¥à)
        this.uploadFile()
      }
    },

    // ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Í¥ÄÎ†® Î©îÏÑúÎìú
    onDragOver() {
      this.isDragging = true
    },

    onDragLeave() {
      this.isDragging = false
    },

    onDrop(event) {
      this.isDragging = false

      if (event.dataTransfer.files.length) {
        this.selectedFile = event.dataTransfer.files[0]
        this.uploadError = ''
        this.uploadSuccess = ''

        // ÌååÏùºÏù¥ ÎìúÎ°≠ÎêòÎ©¥ ÏûêÎèôÏúºÎ°ú ÏóÖÎ°úÎìú ÏãúÏûë
        this.showNotification('ÌååÏùº ÏóÖÎ°úÎìú Ï§ë...', 10000) // ÏóÖÎ°úÎìú Ï§ë ÏïåÎ¶º (ÏµúÎåÄ 10Ï¥à)
        this.uploadFile()
      }
    },

    openFilePicker(event) {
      // Ïù¥ÎØ∏ ÏÑ†ÌÉùÎêú ÌååÏùºÏù¥ ÌëúÏãúÎêòÎäî ÏòÅÏó≠ ÎòêÎäî ÏÇ≠Ï†ú Î≤ÑÌäºÏùÑ ÌÅ¥Î¶≠Ìïú Í≤ΩÏö∞
      // ÌååÏùº ÌîºÏª§Í∞Ä Ïó¥Î¶¨ÏßÄ ÏïäÎèÑÎ°ù Î∞©ÏßÄ
      if (event.target.closest('.selected-file') ||
          event.target.closest('.file-select-button') ||
          event.target.closest('.clear-file-button')) {
        return;
      }

      // ÌååÏùº ÏûÖÎ†• ÌïÑÎìú ÌÅ¥Î¶≠
      this.$refs.fileInput.click();
    },

    clearSelectedFile() {
      this.selectedFile = null
      this.$refs.fileInput.value = ''
      this.uploadError = ''
      this.uploadSuccess = ''
    },

    async uploadFile() {
      if (!this.selectedFile) {
        this.uploadError = 'ÏóÖÎ°úÎìúÌï† ÌååÏùºÏùÑ ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.'
        this.showNotification('ÏóÖÎ°úÎìúÌï† ÌååÏùºÏùÑ ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.', 3000, true)
        return
      }

      this.isUploading = true
      this.uploadProgress = 0
      this.uploadError = ''
      this.uploadSuccess = ''

      const formData = new FormData()
      formData.append('file', this.selectedFile)

      // ÏÜåÏºì Ïó∞Í≤∞ Ïãú ÏÑ§Ï†ïÎêú ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ ÏÇ¨Ïö©
      const userName = this.userName || 'ÎÇ¥ ÌååÏùº'
      formData.append('userName', userName)

      // ÎîîÎ≤ÑÍπÖÏö© Î°úÍ∑∏
      console.log('ÌååÏùº ÏóÖÎ°úÎìú Ïãú ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ:', userName)

      // Socket ID Ï∂îÍ∞Ä (ÏûêÏã†Ïù¥ ÏóÖÎ°úÎìúÌïú ÌååÏùº ÏãùÎ≥ÑÏö©)
      if (this.socket && this.socket.connected) {
        formData.append('socketId', this.socket.id)
      }

      // ÌòÑÏû¨ URLÏóê userName ÌååÎùºÎØ∏ÌÑ∞ ÏóÖÎç∞Ïù¥Ìä∏
      const urlParams = new URLSearchParams(window.location.search);
      const userNameFromUrl = urlParams.get('userName');

      // URL ÌååÎùºÎØ∏ÌÑ∞ÏôÄ ÌòÑÏû¨ ÏÇ¨Ïö©Ïûê Ïù¥Î¶ÑÏù¥ Îã§Î•¥Î©¥ URL ÏóÖÎç∞Ïù¥Ìä∏
      if (userNameFromUrl !== userName) {
        // ÌòÑÏû¨ URL Í∞ÄÏ†∏Ïò§Í∏∞
        const currentUrl = new URL(window.location.href);
        // userName ÌååÎùºÎØ∏ÌÑ∞ ÏÑ§Ï†ï
        currentUrl.searchParams.set('userName', userName);
        // Î∏åÎùºÏö∞Ï†Ä ÌûàÏä§ÌÜ†Î¶¨ ÏóÖÎç∞Ïù¥Ìä∏ (ÌéòÏù¥ÏßÄ ÏÉàÎ°úÍ≥†Ïπ® ÏóÜÏù¥)
        window.history.replaceState({}, '', currentUrl.toString());
      }

      console.log('ÏóÖÎ°úÎìú Ïãú ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ:', userName)

      console.log('ÏóÖÎ°úÎìú ÏãúÏûë:', {
        fileName: this.selectedFile.name,
        fileSize: this.selectedFile.size,
        fileType: this.selectedFile.type,
        roomCode: this.roomCode
      })

      try {
        console.log('ÏöîÏ≤≠ URL:', `${this.apiUrl}/api/rooms/${this.roomCode}/files`)

        const response = await axios.post(
          `${this.apiUrl}/api/rooms/${this.roomCode}/files`,
          formData,
          {
            headers: {
              'Content-Type': 'multipart/form-data'
            },
            withCredentials: true,
            timeout: 60000, // 60Ï¥àÎ°ú ÌÉÄÏûÑÏïÑÏõÉ ÏãúÍ∞Ñ Ï¶ùÍ∞Ä
            onUploadProgress: progressEvent => {
              const percentCompleted = Math.round(
                (progressEvent.loaded * 100) / progressEvent.total
              )
              this.uploadProgress = percentCompleted
              console.log('ÏóÖÎ°úÎìú ÏßÑÌñâÎ•†:', percentCompleted + '%')
            }
          }
        )

        console.log('ÏóÖÎ°úÎìú ÏùëÎãµ:', response.data)

        const successMessage = response.data.message || 'ÌååÏùºÏù¥ ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏóÖÎ°úÎìúÎêòÏóàÏäµÎãàÎã§.'
        this.uploadSuccess = successMessage
        this.uploadProgress = 100
        this.showNotification(successMessage, 3000)

        this.selectedFile = null
        if (this.$refs.fileInput) {
          this.$refs.fileInput.value = ''
        }

        // ÏóÖÎ°úÎìúÎêú ÌååÏùº Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
        const uploadedFile = response.data.file

        // ÌååÏùºÏóê Í≥†Ïú† ÌÇ§ Ï∂îÍ∞Ä
        if (uploadedFile) {
          uploadedFile._uniqueKey = generateUUID()

          // ÌååÏùº Î™©Î°ùÏóê ÏÉà ÌååÏùº Ï∂îÍ∞Ä (Ï§ëÎ≥µ Î∞©ÏßÄ)
          const existingFileIndex = this.files.findIndex(file => file.filename === uploadedFile.filename)

          if (existingFileIndex !== -1) {
            // Í∏∞Ï°¥ ÌååÏùºÏùò Í≥†Ïú† ÌÇ§ Ïú†ÏßÄ
            uploadedFile._uniqueKey = this.files[existingFileIndex]._uniqueKey
            // Í∏∞Ï°¥ ÌååÏùº ÏóÖÎç∞Ïù¥Ìä∏
            this.files.splice(existingFileIndex, 1, uploadedFile)
          } else {
            // ÏÉà ÌååÏùº Ï∂îÍ∞Ä
            this.files.push(uploadedFile)
          }

          // ÌååÏùº Î™©Î°ù Ï†ïÎ†¨ (ÏµúÏã† ÏóÖÎ°úÎìú Ïàú)
          this.files.sort((a, b) => b.uploadedAt - a.uploadedAt)
        }
      } catch (error) {
        console.error('ÌååÏùº ÏóÖÎ°úÎìú Ïò§Î•ò:', error)

        if (error.response) {
          console.error('ÏÑúÎ≤Ñ ÏùëÎãµ Ïò§Î•ò:', {
            status: error.response.status,
            data: error.response.data,
            headers: error.response.headers
          })
        } else if (error.request) {
          console.error('ÏöîÏ≤≠ÏùÄ Ï†ÑÏÜ°ÎêêÏúºÎÇò ÏùëÎãµ ÏóÜÏùå:', error.request)
        } else {
          console.error('ÏöîÏ≤≠ ÏÉùÏÑ± Ï§ë Ïò§Î•ò:', error.message)
        }

        const errorMessage = error.response?.data?.message || 'ÌååÏùº ÏóÖÎ°úÎìúÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.'
        this.uploadError = errorMessage
        this.uploadProgress = 0
        this.showNotification(errorMessage, 5000, true)
      } finally {
        this.isUploading = false
      }
    },

    // ÌååÏùº Î™©Î°ù Í¥ÄÎ†® Î©îÏÑúÎìú
    async loadFiles() {
      this.isLoading = true

      try {
        const response = await axios.get(`${this.apiUrl}/api/rooms/${this.roomCode}/files`, {
          withCredentials: true
        })
        const files = response.data.files || []

        // Ï§ëÎ≥µ ÌååÏùº Ï†úÍ±∞ Î∞è Í≥†Ïú† ID Ìï†Îãπ
        const filenameMap = new Map()

        // ÌååÏùºÎ™ÖÏúºÎ°ú Ï§ëÎ≥µ Ï†úÍ±∞
        files.forEach(file => {
          // Ïù¥ÎØ∏ ÎèôÏùºÌïú ÌååÏùºÎ™ÖÏù¥ ÏûàÎäî Í≤ΩÏö∞ Í∞ÄÏû• ÏµúÍ∑º ÌååÏùºÎ°ú ÎåÄÏ≤¥
          if (!filenameMap.has(file.filename) || file.uploadedAt > filenameMap.get(file.filename).uploadedAt) {
            filenameMap.set(file.filename, file)
          }
        })

        // Ï§ëÎ≥µÏù¥ Ï†úÍ±∞Îêú ÌååÏùº Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞
        const uniqueFiles = Array.from(filenameMap.values())

        console.log('ÏÑúÎ≤ÑÏóêÏÑú Î∞õÏùÄ ÌååÏùº Î™©Î°ù:', uniqueFiles);

        // Í∞Å ÌååÏùºÏóê Í≥†Ïú† ID Ìï†Îãπ (Ïù¥ÎØ∏ ÏûàÎäî Í≤ΩÏö∞ Ïú†ÏßÄ)
        this.files = uniqueFiles.map(file => {
          // ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ Ï≤òÎ¶¨ (Ìï≠ÏÉÅ RedisÏóê Ï†ÄÏû•Îêú Ïù¥Î¶Ñ ÏÇ¨Ïö©)
          if (file.userName === undefined || file.userName === null || file.userName === '') {
            console.log('ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ ÏóÜÏùå, Í∏∞Î≥∏Í∞í ÏÇ¨Ïö©:', file.originalName);
            file.userName = 'ÏùµÎ™Ö';
          }
          // ÏÇ¨Ïö©Ïûê Ïù¥Î¶ÑÏù¥ ÏûàÎäî Í≤ΩÏö∞ Í∑∏ÎåÄÎ°ú ÏÇ¨Ïö©
          console.log('ÌååÏùº Ï†ïÎ≥¥:', file.originalName, 'ÏóÖÎ°úÎçî:', file.userName);
          return {
            ...file,
            _uniqueKey: generateUUID() // Î†åÎçîÎßÅÏö© Í≥†Ïú† ÌÇ§
          };
        })
      } catch (error) {
        console.error('ÌååÏùº Î™©Î°ù Î°úÎìú Ïò§Î•ò:', error)
        this.files = []
      } finally {
        this.isLoading = false
      }
    },

    // ÌååÏùº Îã§Ïö¥Î°úÎìú Î©îÏÑúÎìú
    async downloadFile(file) {
      try {
        this.showNotification('ÌååÏùº Îã§Ïö¥Î°úÎìú Ï§ë...', 5000)

        // Îã§Ïö¥Î°úÎìú URL ÏÉùÏÑ± (ID ÏÇ¨Ïö©)
        const downloadUrl = `${this.apiUrl}/api/rooms/${this.roomCode}/files/download/${file.id}?download=true`
        console.log('ÌååÏùº Îã§Ïö¥Î°úÎìú ÏãúÎèÑ:', file.originalName, downloadUrl)

        // axiosÎ•º ÏÇ¨Ïö©ÌïòÏó¨ ÌååÏùº Îã§Ïö¥Î°úÎìú
        const response = await axios({
          url: downloadUrl,
          method: 'GET',
          responseType: 'blob',
          withCredentials: true,
          timeout: 60000 // 60Ï¥à ÌÉÄÏûÑÏïÑÏõÉ
        })

        // ÌååÏùº ÌÉÄÏûÖ Í≤∞Ï†ï (MIME ÌÉÄÏûÖÏù¥ ÏûàÏúºÎ©¥ ÏÇ¨Ïö©, ÏóÜÏúºÎ©¥ Í∏∞Î≥∏Í∞í)
        const contentType = file.mimetype || 'application/octet-stream'

        // Blob ÏÉùÏÑ±
        const blob = new Blob([response.data], { type: contentType })

        // Îã§Ïö¥Î°úÎìú ÎßÅÌÅ¨ ÏÉùÏÑ±
        const url = window.URL.createObjectURL(blob)
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', file.originalName)
        document.body.appendChild(link)

        // Îã§Ïö¥Î°úÎìú ÏãúÏûë
        link.click()

        // ÏïΩÍ∞ÑÏùò ÏßÄÏó∞ ÌõÑ ÎßÅÌÅ¨ Ï†úÍ±∞ (Î∏åÎùºÏö∞Ï†ÄÍ∞Ä Îã§Ïö¥Î°úÎìúÎ•º ÏãúÏûëÌï† ÏãúÍ∞Ñ Ï†úÍ≥µ)
        setTimeout(() => {
          window.URL.revokeObjectURL(url)
          document.body.removeChild(link)
        }, 100)

        this.showNotification('ÌååÏùº Îã§Ïö¥Î°úÎìúÍ∞Ä ÏãúÏûëÎêòÏóàÏäµÎãàÎã§.', 3000)
      } catch (error) {
        console.error('ÌååÏùº Îã§Ïö¥Î°úÎìú Ïò§Î•ò:', error)
        console.error('Ïò§Î•ò ÏÑ∏Î∂Ä Ï†ïÎ≥¥:', error.message, error.stack)
        this.showNotification('ÌååÏùº Îã§Ïö¥Î°úÎìúÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.', 5000, true)
      }
    },

    // ÌååÏùº ÏÇ≠Ï†ú Î©îÏÑúÎìú
    async deleteFile(file) {
      if (!confirm(`ÌååÏùº "${file.originalName}"ÏùÑ(Î•º) ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?`)) {
        return
      }

      try {
        const response = await axios.delete(`${this.apiUrl}/api/rooms/${this.roomCode}/files/${file.id}`, {
          withCredentials: true
        })

        // ÌååÏùº Î™©Î°ùÏóêÏÑú ÏÇ≠Ï†ú
        const fileIndex = this.files.findIndex(f => f.id === file.id)
        if (fileIndex !== -1) {
          this.files.splice(fileIndex, 1)
        }

        this.showNotification(response.data.message || 'ÌååÏùºÏù¥ ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.')
      } catch (error) {
        console.error('ÌååÏùº ÏÇ≠Ï†ú Ïò§Î•ò:', error)
        this.showNotification('ÌååÏùº ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
      }
    },

    // Ï†ÑÏ≤¥ ÌååÏùº Îã§Ïö¥Î°úÎìú Î©îÏÑúÎìú
    async downloadAllFiles() {
      if (this.files.length === 0) {
        this.showNotification('Îã§Ïö¥Î°úÎìúÌï† ÌååÏùºÏù¥ ÏóÜÏäµÎãàÎã§.', 3000, true)
        return
      }

      this.isDownloadingAll = true
      this.showNotification('Ï†ÑÏ≤¥ ÌååÏùº Îã§Ïö¥Î°úÎìú Ï§ÄÎπÑ Ï§ë...', 10000)

      try {
        // ÌòÑÏû¨ ÌëúÏãúÎêú ÌååÏùº Î™©Î°ù ÏÇ¨Ïö© (API Ìò∏Ï∂ú ÎåÄÏã†)
        console.log('ÌòÑÏû¨ ÌååÏùº Î™©Î°ù ÏÇ¨Ïö©:', this.files)

        const filesToDownload = this.files || []

        if (filesToDownload.length === 0) {
          this.showNotification('Îã§Ïö¥Î°úÎìúÌï† ÌååÏùºÏù¥ ÏóÜÏäµÎãàÎã§.', 3000, true)
          this.isDownloadingAll = false
          return
        }

        this.showNotification(`${filesToDownload.length}Í∞ú ÌååÏùº Îã§Ïö¥Î°úÎìú Ï§ë...`, 30000)

        // Í∞Å ÌååÏùºÏùÑ ÏàúÏ∞®Ï†ÅÏúºÎ°ú Îã§Ïö¥Î°úÎìú
        let downloadedCount = 0
        let failedCount = 0

        for (let i = 0; i < filesToDownload.length; i++) {
          const file = filesToDownload[i]
          try {
            // Îã§Ïö¥Î°úÎìú URL ÏÉùÏÑ±
            const downloadUrl = `${this.apiUrl}/api/rooms/${this.roomCode}/files/download/${file.id}`
            console.log(`ÌååÏùº Îã§Ïö¥Î°úÎìú ÏãúÎèÑ (${i+1}/${filesToDownload.length}):`, file.originalName, downloadUrl)

            // ÌååÏùº Îã§Ïö¥Î°úÎìú
            const fileResponse = await axios({
              url: downloadUrl,
              method: 'GET',
              responseType: 'blob',
              withCredentials: true,
              timeout: 60000 // 60Ï¥àÎ°ú ÌÉÄÏûÑÏïÑÏõÉ Ï¶ùÍ∞Ä
            })

            // ÌååÏùº ÌÉÄÏûÖ Í≤∞Ï†ï (MIME ÌÉÄÏûÖÏù¥ ÏûàÏúºÎ©¥ ÏÇ¨Ïö©, ÏóÜÏúºÎ©¥ Í∏∞Î≥∏Í∞í)
            const contentType = file.mimetype || 'application/octet-stream'

            // Blob ÏÉùÏÑ±
            const blob = new Blob([fileResponse.data], { type: contentType })

            // Îã§Ïö¥Î°úÎìú ÎßÅÌÅ¨ ÏÉùÏÑ±
            const url = window.URL.createObjectURL(blob)
            const link = document.createElement('a')
            link.href = url
            link.setAttribute('download', file.originalName)

            // ÎßÅÌÅ¨Î•º DOMÏóê Ï∂îÍ∞ÄÌïòÍ≥† ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏ Î∞úÏÉù
            document.body.appendChild(link)
            link.click()

            // ÏïΩÍ∞ÑÏùò ÏßÄÏó∞ ÌõÑ ÎßÅÌÅ¨ Ï†úÍ±∞ (Î∏åÎùºÏö∞Ï†ÄÍ∞Ä Îã§Ïö¥Î°úÎìúÎ•º ÏãúÏûëÌï† ÏãúÍ∞Ñ Ï†úÍ≥µ)
            setTimeout(() => {
              window.URL.revokeObjectURL(url)
              document.body.removeChild(link)
            }, 100)

            downloadedCount++

            // ÏßÑÌñâ ÏÉÅÌô© ÏóÖÎç∞Ïù¥Ìä∏
            if (i < filesToDownload.length - 1) {
              this.showNotification(`ÌååÏùº Îã§Ïö¥Î°úÎìú Ï§ë... (${downloadedCount}/${filesToDownload.length})`, 30000)
            }

            // Ïû†Ïãú ÎåÄÍ∏∞ (Î∏åÎùºÏö∞Ï†Ä Îã§Ïö¥Î°úÎìú Ï≤òÎ¶¨ ÏãúÍ∞Ñ Ï†úÍ≥µ)
            await new Promise(resolve => setTimeout(resolve, 1000))
          } catch (fileError) {
            console.error(`ÌååÏùº Îã§Ïö¥Î°úÎìú Ïò§Î•ò (${file.originalName}):`, fileError)
            console.error('Ïò§Î•ò ÏÑ∏Î∂Ä Ï†ïÎ≥¥:', fileError.message, fileError.stack)
            failedCount++
          }
        }

        if (failedCount > 0) {
          this.showNotification(`${downloadedCount}Í∞ú ÌååÏùº Îã§Ïö¥Î°úÎìú ÏôÑÎ£å, ${failedCount}Í∞ú Ïã§Ìå®`, 5000, true)
        } else {
          this.showNotification(`${downloadedCount}Í∞ú ÌååÏùº Îã§Ïö¥Î°úÎìúÍ∞Ä ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§.`, 5000)
        }
      } catch (error) {
        console.error('Ï†ÑÏ≤¥ ÌååÏùº Îã§Ïö¥Î°úÎìú Ïò§Î•ò:', error)
        console.error('Ïò§Î•ò ÏÑ∏Î∂Ä Ï†ïÎ≥¥:', error.message, error.stack)
        this.showNotification('Ï†ÑÏ≤¥ ÌååÏùº Îã§Ïö¥Î°úÎìúÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.', 5000, true)
      } finally {
        this.isDownloadingAll = false
      }
    },

    // ÏïåÎ¶º ÌëúÏãú Î©îÏÑúÎìú
    showNotification(message, duration = 3000, isError = false) {
      // Ïù¥Ï†Ñ ÌÉÄÏù¥Î®∏Í∞Ä ÏûàÎäî Í≤ΩÏö∞ Ï∑®ÏÜå
      if (this.notification.timeout) {
        clearTimeout(this.notification.timeout)
      }

      // ÏïåÎ¶º ÌëúÏãú
      this.notification.message = message
      this.notification.show = true
      this.notification.isError = isError

      // ÏùºÏ†ï ÏãúÍ∞Ñ ÌõÑ ÏïåÎ¶º Ïà®Í∏∞Í∏∞
      this.notification.timeout = setTimeout(() => {
        this.notification.show = false
      }, duration)
    },

    // ÌååÏùº ÌÅ¨Í∏∞ Ìè¨Îß∑ÌåÖ
    formatFileSize(bytes) {
      if (bytes === 0) return '0 Bytes'

      const k = 1024
      const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB']
      const i = Math.floor(Math.log(bytes) / Math.log(k))

      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
    },

    // ÎÇ†Ïßú Ìè¨Îß∑ÌåÖ
    formatDate(timestamp) {
      if (!timestamp) return 'Ïïå Ïàò ÏóÜÏùå'

      const date = new Date(timestamp)
      const now = new Date()

      // Ïò§Îäò ÎÇ†ÏßúÏù∏ Í≤ΩÏö∞ ÏãúÍ∞ÑÎßå ÌëúÏãú
      if (date.toDateString() === now.toDateString()) {
        return `Ïò§Îäò ${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`
      }

      // Ïñ¥Ï†ú ÎÇ†ÏßúÏù∏ Í≤ΩÏö∞ 'Ïñ¥Ï†ú'Î°ú ÌëúÏãú
      const yesterday = new Date(now)
      yesterday.setDate(now.getDate() - 1)
      if (date.toDateString() === yesterday.toDateString()) {
        return `Ïñ¥Ï†ú ${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`
      }

      // Í∑∏ Ïô∏Ïùò Í≤ΩÏö∞ ÎÇ†ÏßúÏôÄ ÏãúÍ∞Ñ Î™®Îëê ÌëúÏãú
      return `${date.getMonth() + 1}/${date.getDate()} ${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`
    },

    // ÌååÏùº ÎßåÎ£å ÏãúÍ∞Ñ Ìè¨Îß∑
    formatExpiryTime(timestamp) {
      if (!timestamp) return 'Ïïå Ïàò ÏóÜÏùå'

      const expiryDate = new Date(timestamp)
      const now = new Date()

      // ÎÇ®ÏùÄ ÏãúÍ∞Ñ Í≥ÑÏÇ∞ (Î∞ÄÎ¶¨Ï¥à)
      const remainingTime = expiryDate - now

      // Ïù¥ÎØ∏ ÎßåÎ£åÎêú Í≤ΩÏö∞
      if (remainingTime <= 0) {
        return 'ÎßåÎ£åÎê®'
      }

      // 1ÏãúÍ∞Ñ Ïù¥ÎÇ¥Ïù∏ Í≤ΩÏö∞ Î∂Ñ Îã®ÏúÑÎ°ú ÌëúÏãú
      if (remainingTime < 3600000) {
        const minutes = Math.ceil(remainingTime / 60000)
        return `${minutes}Î∂Ñ ÌõÑ`
      }

      // Ïò§Îäò ÎßåÎ£åÎêòÎäî Í≤ΩÏö∞ ÏãúÍ∞ÑÎßå ÌëúÏãú
      if (expiryDate.toDateString() === now.toDateString()) {
        return `Ïò§Îäò ${expiryDate.getHours().toString().padStart(2, '0')}:${expiryDate.getMinutes().toString().padStart(2, '0')}`
      }

      // ÎÇ¥Ïùº ÎßåÎ£åÎêòÎäî Í≤ΩÏö∞ 'ÎÇ¥Ïùº'Î°ú ÌëúÏãú
      const tomorrow = new Date(now)
      tomorrow.setDate(now.getDate() + 1)
      if (expiryDate.toDateString() === tomorrow.toDateString()) {
        return `ÎÇ¥Ïùº ${expiryDate.getHours().toString().padStart(2, '0')}:${expiryDate.getMinutes().toString().padStart(2, '0')}`
      }

      // Í∑∏ Ïô∏Ïùò Í≤ΩÏö∞ ÎÇ†ÏßúÏôÄ ÏãúÍ∞Ñ Î™®Îëê ÌëúÏãú
      return `${expiryDate.getMonth() + 1}/${expiryDate.getDate()} ${expiryDate.getHours().toString().padStart(2, '0')}:${expiryDate.getMinutes().toString().padStart(2, '0')}`
    },

    // ÌååÏùºÏù¥ Í≥ß ÎßåÎ£åÎêòÎäîÏßÄ ÌôïÏù∏ (30Î∂Ñ Ïù¥ÎÇ¥)
    isExpiringSoon(file) {
      if (!file.expiresAt) return false

      const expiryDate = new Date(file.expiresAt)
      const now = new Date()

      // ÎÇ®ÏùÄ ÏãúÍ∞Ñ Í≥ÑÏÇ∞ (Î∞ÄÎ¶¨Ï¥à)
      const remainingTime = expiryDate - now

      // 30Î∂Ñ(1800000 Î∞ÄÎ¶¨Ï¥à) Ïù¥ÎÇ¥Ïù∏ Í≤ΩÏö∞ true Î∞òÌôò
      return remainingTime > 0 && remainingTime < 1800000
    },

    // ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ Ìé∏Ïßë ÏãúÏûë
    startEditUserName() {
      this.editUserName = this.userName
      this.isEditingUserName = true

      // Îã§Ïùå Ìã±ÏóêÏÑú ÏûÖÎ†• ÌïÑÎìúÏóê Ìè¨Ïª§Ïä§
      this.$nextTick(() => {
        if (this.$refs.userNameInput) {
          this.$refs.userNameInput.focus()
        }
      })
    },

    // ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ Ï†ÄÏû•
    saveUserName() {
      // Îπà Î¨∏ÏûêÏó¥Ïù¥Î©¥ Í∏∞Î≥∏Í∞í ÏÇ¨Ïö©
      if (!this.editUserName.trim()) {
        this.editUserName = 'ÎÇ¥ ÌååÏùº'
      }

      // Ïù¥Î¶ÑÏù¥ Î≥ÄÍ≤ΩÎêú Í≤ΩÏö∞ÏóêÎßå Ï≤òÎ¶¨
      if (this.userName !== this.editUserName) {
        this.userName = this.editUserName.trim()

        // URL ÌååÎùºÎØ∏ÌÑ∞ ÏóÖÎç∞Ïù¥Ìä∏
        const currentUrl = new URL(window.location.href)
        currentUrl.searchParams.set('userName', this.userName)
        window.history.replaceState({}, '', currentUrl.toString())

        // ÏïåÎ¶º ÌëúÏãú
        this.showNotification(`ÏÇ¨Ïö©Ïûê Ïù¥Î¶ÑÏù¥ '${this.userName}'(Ïúº)Î°ú Î≥ÄÍ≤ΩÎêòÏóàÏäµÎãàÎã§.`)
      }

      this.isEditingUserName = false
    },

    // QR ÏΩîÎìú Í¥ÄÎ†® Î©îÏÑúÎìú
    generateQrCode() {
      try {
        // ÌòÑÏû¨ URLÏóêÏÑú fromQr ÌååÎùºÎØ∏ÌÑ∞ Ï†úÍ±∞ (ÏûàÎäî Í≤ΩÏö∞)
        const currentUrl = new URL(window.location.href);
        currentUrl.searchParams.delete('fromQr');

        // QR ÏΩîÎìúÏö© URL ÏÉùÏÑ± (fromQr ÌååÎùºÎØ∏ÌÑ∞ Ï∂îÍ∞Ä)
        const qrUrl = new URL(currentUrl.toString());
        qrUrl.searchParams.set('fromQr', 'true');
        this.qrCodeUrl = qrUrl.toString();
      } catch (error) {
        console.error('QR ÏΩîÎìú URL ÏÉùÏÑ± Ïò§Î•ò:', error);
      }
    },

    // QR ÏΩîÎìú Î™®Îã¨ ÌëúÏãú
    showQrCode() {
      this.showQrCodeModal = true;

      // QR ÏΩîÎìú ÏÉùÏÑ± (Î™®Îã¨Ïù¥ ÌëúÏãúÎêú ÌõÑ)
      this.$nextTick(() => {
        this.generateQrCodeInModal();
      });
    },

    // QR ÏΩîÎìú Î™®Îã¨ Îã´Í∏∞
    closeQrCodeModal() {
      this.showQrCodeModal = false;
    },

    // Î™®Îã¨ ÎÇ¥ QR ÏΩîÎìú ÏÉùÏÑ±
    async generateQrCodeInModal() {
      try {
        if (this.$refs.qrcodeLarge) {
          const qrCodeElement = this.$refs.qrcodeLarge;

          // Í∏∞Ï°¥ ÎÇ¥Ïö© Ï†úÍ±∞
          qrCodeElement.innerHTML = '';

          // QR ÏΩîÎìú ÏÉùÏÑ± Ï§ë Î°úÎî© ÌëúÏãú
          const loadingElement = document.createElement('div');
          loadingElement.className = 'qr-loading';
          loadingElement.textContent = 'QR ÏΩîÎìú ÏÉùÏÑ± Ï§ë...';
          qrCodeElement.appendChild(loadingElement);

          // QR ÏΩîÎìú Ï∫îÎ≤ÑÏä§ ÏÉùÏÑ±
          const canvas = document.createElement('canvas');

          // QR ÏΩîÎìú ÏÉùÏÑ± (ÎπÑÎèôÍ∏∞)
          await this.$qrcode.toCanvas(canvas, this.qrCodeUrl, {
            width: 400,
            margin: 4,
            color: {
              dark: '#000000',
              light: '#ffffff'
            },
            errorCorrectionLevel: 'H' // ÎÜíÏùÄ Ïò§Î•ò ÏàòÏ†ï Î†àÎ≤®
          });

          // Î°úÎî© ÏöîÏÜå Ï†úÍ±∞
          qrCodeElement.innerHTML = '';

          // Ï∫îÎ≤ÑÏä§ Ï∂îÍ∞Ä
          canvas.className = 'qr-code-img-large';
          qrCodeElement.appendChild(canvas);

          // ÏïÑÎûòÏóê URL ÌÖçÏä§Ìä∏ Ï∂îÍ∞Ä
          const urlText = document.createElement('div');
          urlText.className = 'qr-url-text';
          urlText.textContent = this.qrCodeUrl;
          qrCodeElement.appendChild(urlText);
        }
      } catch (error) {
        console.error('QR ÏΩîÎìú ÏÉùÏÑ± Ïò§Î•ò:', error);

        // Ïò§Î•ò Î∞úÏÉù Ïãú ÏÇ¨Ïö©ÏûêÏóêÍ≤å ÏïåÎ¶º
        if (this.$refs.qrcodeLarge) {
          this.$refs.qrcodeLarge.innerHTML = '<div class="qr-error">QR ÏΩîÎìú ÏÉùÏÑ± Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.</div>';
        }
      }
    },

    saveQrUserName() {
      if (this.qrUserName.trim()) {
        // ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ Ï†ÄÏû•
        this.userName = this.qrUserName.trim();

        // URL ÌååÎùºÎØ∏ÌÑ∞ ÏóÖÎç∞Ïù¥Ìä∏
        const currentUrl = new URL(window.location.href);
        currentUrl.searchParams.set('userName', this.userName);
        currentUrl.searchParams.delete('fromQr'); // fromQr ÌååÎùºÎØ∏ÌÑ∞ Ï†úÍ±∞
        window.history.replaceState({}, '', currentUrl.toString());

        // ÏÜåÏºì Ïó∞Í≤∞ ÏóÖÎç∞Ïù¥Ìä∏
        if (this.socket && this.socket.connected) {
          this.socket.emit('updateUserName', {
            roomCode: this.roomCode,
            userName: this.userName
          });
        }

        // Î™®Îã¨ Îã´Í∏∞
        this.showQrUserNameModal = false;

        // ÌôòÏòÅ Î©îÏãúÏßÄ ÌëúÏãú
        this.showNotification(`ÌôòÏòÅÌï©ÎãàÎã§, ${this.userName}Îãò!`, 3000);
      } else {
        // Îπà Ïù¥Î¶ÑÏù∏ Í≤ΩÏö∞ Í∏∞Î≥∏Í∞í ÏÑ§Ï†ï
        this.qrUserName = 'Î∞©Î¨∏Ïûê';
      }
    },

    // ÌååÏùº ÎØ∏Î¶¨Î≥¥Í∏∞ Î©îÏÑúÎìú
    async previewFile(file) {
      // ÎØ∏Î¶¨Î≥¥Í∏∞ Î™®Îã¨ Ï¥àÍ∏∞Ìôî
      this.previewModal.file = file
      this.previewModal.previewUrl = ''
      this.previewModal.officeViewerUrl = ''
      this.previewModal.textContent = null
      this.previewModal.isLoading = true
      this.previewModal.show = true

      try {
        // Îã§Ïö¥Î°úÎìú URL ÏÉùÏÑ±
        const downloadUrl = `${this.apiUrl}/api/rooms/${this.roomCode}/files/download/${file.id}`

        // ÌååÏùº Ïú†ÌòïÏóê Îî∞Îùº Îã§Î•∏ Ï≤òÎ¶¨
        if (this.isImageFile(file) || this.isPdfFile(file) || this.isVideoFile(file) || this.isAudioFile(file)) {
          // ÎØ∏ÎîîÏñ¥ ÌååÏùºÏùÄ URLÎ°ú ÏßÅÏ†ë ÌëúÏãú
          this.previewModal.previewUrl = downloadUrl
        } else if (this.isExcelFile(file) || this.isOfficeFile(file)) {
          // Excel Î∞è Office ÌååÏùºÏùÄ Google Docs ViewerÎ•º ÏÇ¨Ïö©ÌïòÏó¨ ÌëúÏãú
          const encodedUrl = encodeURIComponent(`${window.location.origin}${downloadUrl}`)
          this.previewModal.officeViewerUrl = `https://docs.google.com/viewer?url=${encodedUrl}&embedded=true`

          // Microsoft Office Online Viewer ÎåÄÏ≤¥ ÏòµÏÖò (ÌïÑÏöîÏãú ÏÇ¨Ïö©)
          // this.previewModal.officeViewerUrl = `https://view.officeapps.live.com/op/embed.aspx?src=${encodedUrl}`
        } else if (this.isTextFile(file)) {
          // ÌÖçÏä§Ìä∏ ÌååÏùºÏùÄ ÎÇ¥Ïö©ÏùÑ Í∞ÄÏ†∏ÏôÄÏÑú ÌëúÏãú
          const response = await axios({
            url: downloadUrl,
            method: 'GET',
            responseType: 'text',
            withCredentials: true
          })
          this.previewModal.textContent = response.data
        }
      } catch (error) {
        console.error('ÌååÏùº ÎØ∏Î¶¨Î≥¥Í∏∞ Ïò§Î•ò:', error)
        this.showNotification('ÌååÏùº ÎØ∏Î¶¨Î≥¥Í∏∞Î•º Î∂àÎü¨Ïò§ÎäîÎç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.', 3000, true)
      } finally {
        this.previewModal.isLoading = false
      }
    },

    // ÎØ∏Î¶¨Î≥¥Í∏∞ Î™®Îã¨ Îã´Í∏∞
    closePreview() {
      this.previewModal.show = false
      this.previewModal.file = null
      this.previewModal.previewUrl = ''
      this.previewModal.textContent = null
    },

    // ÌååÏùº Ïú†Ìòï ÌôïÏù∏ Î©îÏÑúÎìú
    isImageFile(file) {
      return file.mimetype && file.mimetype.startsWith('image/')
    },

    isPdfFile(file) {
      return file.mimetype === 'application/pdf'
    },

    isVideoFile(file) {
      return file.mimetype && file.mimetype.startsWith('video/')
    },

    isAudioFile(file) {
      return file.mimetype && file.mimetype.startsWith('audio/')
    },

    isExcelFile(file) {
      const excelTypes = [
        'application/vnd.ms-excel',
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
        'application/vnd.oasis.opendocument.spreadsheet'
      ]
      return excelTypes.includes(file.mimetype) ||
        (file.originalName && /\.(xlsx|xls|csv|ods)$/i.test(file.originalName))
    },

    isOfficeFile(file) {
      const officeTypes = [
        // Word
        'application/msword',
        'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
        // PowerPoint
        'application/vnd.ms-powerpoint',
        'application/vnd.openxmlformats-officedocument.presentationml.presentation'
      ]
      return officeTypes.includes(file.mimetype) ||
        (file.originalName && /\.(doc|docx|ppt|pptx)$/i.test(file.originalName))
    },

    isTextFile(file) {
      const textTypes = [
        'text/plain', 'text/html', 'text/css', 'text/javascript',
        'application/json', 'application/xml', 'application/javascript'
      ]
      return textTypes.includes(file.mimetype)
    },

    // Socket.IO Ïó∞Í≤∞ Í¥ÄÎ†® Î©îÏÑúÎìú
    connectSocket() {
      // Ïù¥ÎØ∏ Ïó∞Í≤∞Îêú ÏÜåÏºìÏù¥ ÏûàÏúºÎ©¥ Ïû¨ÏÇ¨Ïö©
      if (this.socket && this.socket.connected) {
        console.log('Ïù¥ÎØ∏ Ïó∞Í≤∞Îêú Socket.IO Ïû¨ÏÇ¨Ïö©');
        return;
      }

      // Socket.IO Ïó∞Í≤∞ ÏÉùÏÑ±
      this.socket = io(this.socketUrl, {
        reconnection: true,        // ÏûêÎèô Ïû¨Ïó∞Í≤∞ ÌôúÏÑ±Ìôî
        reconnectionAttempts: 20,  // ÏµúÎåÄ 20Î≤à Ïû¨Ïó∞Í≤∞ ÏãúÎèÑ
        reconnectionDelay: 1000,   // Ïû¨Ïó∞Í≤∞ ÏãúÎèÑ Í∞ÑÍ≤© (1Ï¥à)
        reconnectionDelayMax: 5000, // ÏµúÎåÄ Ïû¨Ïó∞Í≤∞ ÏßÄÏó∞ ÏãúÍ∞Ñ (5Ï¥à)
        timeout: 30000,            // Ïó∞Í≤∞ ÌÉÄÏûÑÏïÑÏõÉ (30Ï¥à)
        transports: ['websocket', 'polling'], // ÏõπÏÜåÏºì Ïö∞ÏÑ†, Ìè¥ÎßÅ Î∞±ÏóÖ
        withCredentials: true,     // Ïù∏Ï¶ù Ï†ïÎ≥¥ Ìè¨Ìï®
        extraHeaders: {            // Ï∂îÍ∞Ä Ìó§Îçî
          'Access-Control-Allow-Origin': window.location.origin
        }
      });

      // Ïó∞Í≤∞ Ïù¥Î≤§Ìä∏
      this.socket.on('connect', () => {
        console.log('Socket.IO Ïó∞Í≤∞ ÏÑ±Í≥µ:', this.socket.id);

        // Ïó∞Í≤∞ ÏÉÅÌÉú ÌëúÏãú
        this.isSocketConnected = true;

        // URLÏóêÏÑú ÏßÅÏ†ë ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ Ï∂îÏ∂ú
        const urlParams = new URLSearchParams(window.location.search);
        const userNameFromUrl = urlParams.get('userName');

        // Ï£ºÏÜåÏ∞ΩÏóêÏÑú ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ Í∞ÄÏ†∏Ïò§Í∏∞
        const userName = userNameFromUrl || this.userName || 'ÎÇ¥ ÌååÏùº';
        console.log('Socket Ïó∞Í≤∞ Ïãú ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ:', userName);

        // ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ ÏÑ§Ï†ï
        this.userName = userName;

        // Î∞©Ïóê Ï∞∏Ïó¨ (ÏÇ¨Ïö©Ïûê Ïù¥Î¶ÑÎèÑ Ï†ÑÏÜ°)
        this.socket.emit('joinRoom', this.roomCode, userName);

        // Ìïë ÌÉÄÏù¥Î®∏ ÏãúÏûë
        this.startPingTimer();
      });

      // Î∞© Ï∞∏Ïó¨ ÌôïÏù∏ Ïù¥Î≤§Ìä∏
      this.socket.on('joinedRoom', (data) => {
        console.log('Î∞© Ï∞∏Ïó¨ ÌôïÏù∏:', data);
      });

      // Ïû¨Ïó∞Í≤∞ Ïù¥Î≤§Ìä∏
      this.socket.on('reconnect', (attemptNumber) => {
        console.log(`Socket.IO Ïû¨Ïó∞Í≤∞ ÏÑ±Í≥µ (ÏãúÎèÑ ${attemptNumber}Î≤àÏß∏)`);

        // Ïó∞Í≤∞ ÏÉÅÌÉú ÌëúÏãú
        this.isSocketConnected = true;

        // Ïû¨Ïó∞Í≤∞ Ïãú Î∞©Ïóê Îã§Ïãú Ï∞∏Ïó¨
        const urlParams = new URLSearchParams(window.location.search);
        const userNameFromUrl = urlParams.get('userName');
        const userName = userNameFromUrl || this.userName || 'ÎÇ¥ ÌååÏùº';

        // ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ ÏÑ§Ï†ï
        this.userName = userName;

        // Î∞©Ïóê Îã§Ïãú Ï∞∏Ïó¨
        this.socket.emit('joinRoom', this.roomCode, userName);

        // Ìïë ÌÉÄÏù¥Î®∏ Ïû¨ÏãúÏûë
        this.startPingTimer();
      });

      // Ïû¨Ïó∞Í≤∞ ÏãúÎèÑ Ïù¥Î≤§Ìä∏
      this.socket.on('reconnect_attempt', (attemptNumber) => {
        console.log(`Socket.IO Ïû¨Ïó∞Í≤∞ ÏãúÎèÑ Ï§ë (${attemptNumber}Î≤àÏß∏)...`);
      });

      // Ïû¨Ïó∞Í≤∞ Ïò§Î•ò Ïù¥Î≤§Ìä∏
      this.socket.on('reconnect_error', (error) => {
        console.error('Socket.IO Ïû¨Ïó∞Í≤∞ Ïò§Î•ò:', error);
      });

      // Ïû¨Ïó∞Í≤∞ Ïã§Ìå® Ïù¥Î≤§Ìä∏
      this.socket.on('reconnect_failed', () => {
        console.error('Socket.IO Ïû¨Ïó∞Í≤∞ Ïã§Ìå® (ÏµúÎåÄ ÏãúÎèÑ ÌöüÏàò Ï¥àÍ≥º)');
        this.isSocketConnected = false;

        // ÏÇ¨Ïö©ÏûêÏóêÍ≤å ÏïåÎ¶º
        this.showNotification('ÏÑúÎ≤Ñ Ïó∞Í≤∞Ïù¥ ÎÅäÍ≤ºÏäµÎãàÎã§. ÌéòÏù¥ÏßÄÎ•º ÏÉàÎ°úÍ≥†Ïπ®Ìï¥ Ï£ºÏÑ∏Ïöî.', 10000, true);
      });

      // ÌååÏùº ÏóÖÎ°úÎìú Ïù¥Î≤§Ìä∏
      this.socket.on('fileUploaded', (data) => {
        console.log('ÏÉà ÌååÏùº ÏóÖÎ°úÎìú ÏïåÎ¶º ÏàòÏã†:', data)

        // ÎÇ¥Í∞Ä ÏóÖÎ°úÎìúÌïú ÌååÏùºÏù¥Î©¥ Î¨¥Ïãú (Ïù¥ÎØ∏ Ï≤òÎ¶¨Îê®)
        if (data.uploaderId === this.socket.id) {
          console.log('ÎÇ¥Í∞Ä ÏóÖÎ°úÎìúÌïú ÌååÏùºÏù¥ÎØÄÎ°ú Ïù¥Î≤§Ìä∏ Î¨¥Ïãú');
          return;
        }

        // ÌååÏùºÏóê Í≥†Ïú† ÌÇ§ Ï∂îÍ∞Ä
        const newFile = {
          ...data.file,
          _uniqueKey: generateUUID()
        }

        // ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ Ï≤òÎ¶¨ (Ìï≠ÏÉÅ RedisÏóê Ï†ÄÏû•Îêú Ïù¥Î¶Ñ ÏÇ¨Ïö©)
        if (newFile.userName === undefined || newFile.userName === null || newFile.userName === '') {
          console.log('Socket Ïù¥Î≤§Ìä∏: ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ ÏóÜÏùå, Í∏∞Î≥∏Í∞í ÏÇ¨Ïö©:', newFile.originalName);
          newFile.userName = 'ÏùµÎ™Ö';
        }

        // ÌååÏùº Î™©Î°ùÏóê ÏÉà ÌååÏùº Ï∂îÍ∞Ä (Ï§ëÎ≥µ Î∞©ÏßÄ)
        const existingFileIndex = this.files.findIndex(file => file.filename === newFile.filename)

        if (existingFileIndex !== -1) {
          // Í∏∞Ï°¥ ÌååÏùºÏùò Í≥†Ïú† ÌÇ§ Ïú†ÏßÄ
          newFile._uniqueKey = this.files[existingFileIndex]._uniqueKey
          // Í∏∞Ï°¥ ÌååÏùº ÏóÖÎç∞Ïù¥Ìä∏
          this.files.splice(existingFileIndex, 1, newFile)
        } else {
          // ÏÉà ÌååÏùº Ï∂îÍ∞Ä
          this.files.push(newFile)

          // Îã§Î•∏ ÏÇ¨Ïö©ÏûêÍ∞Ä ÏóÖÎ°úÎìúÌïú ÌååÏùºÏù¥Î©¥ ÏïåÎ¶º ÌëúÏãú
          this.showNotification(`${newFile.userName}ÎãòÏù¥ ÏÉà ÌååÏùºÏùÑ ÏóÖÎ°úÎìúÌñàÏäµÎãàÎã§.`, 3000)
        }

        // ÌååÏùº Î™©Î°ù Ï†ïÎ†¨ (ÏµúÏã† ÏóÖÎ°úÎìú Ïàú)
        this.files.sort((a, b) => b.uploadedAt - a.uploadedAt)
      })

      // ÌååÏùº ÏÇ≠Ï†ú Ïù¥Î≤§Ìä∏
      this.socket.on('fileDeleted', (data) => {
        console.log('ÌååÏùº ÏÇ≠Ï†ú ÏïåÎ¶º ÏàòÏã†:', data)

        // ÌååÏùº Î™©Î°ùÏóêÏÑú ÏÇ≠Ï†ú
        const fileIndex = this.files.findIndex(file =>
          (file.id && file.id === data.id) ||
          file.filename === data.filename
        )

        if (fileIndex !== -1) {
          const deletedFile = this.files[fileIndex];
          this.files.splice(fileIndex, 1)

          // ÏÇ≠Ï†ú Ïù¥Ïú†Ïóê Îî∞Îùº Îã§Î•∏ ÏïåÎ¶º ÌëúÏãú
          if (data.reason === 'expired') {
            this.showNotification(`"${deletedFile.originalName}" ÌååÏùºÏù¥ ÎßåÎ£åÎêòÏñ¥ ÏûêÎèôÏúºÎ°ú ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.`, 5000, true);
          } else {
            this.showNotification('ÌååÏùºÏù¥ ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.');
          }
        }
      })

      // Ïó∞Í≤∞ Ïò§Î•ò Ïù¥Î≤§Ìä∏
      this.socket.on('connect_error', (error) => {
        console.error('Socket.IO Ïó∞Í≤∞ Ïò§Î•ò:', error);
        this.isSocketConnected = false;
      });

      // Ïó∞Í≤∞ Ìï¥Ï†ú Ïù¥Î≤§Ìä∏
      this.socket.on('disconnect', (reason) => {
        console.log('Socket.IO Ïó∞Í≤∞ Ìï¥Ï†ú:', reason);
        this.isSocketConnected = false;

        // Ìïë ÌÉÄÏù¥Î®∏ Ï†ïÎ¶¨
        if (this.pingInterval) {
          clearInterval(this.pingInterval);
          this.pingInterval = null;
        }

        // ÏûêÎèôÏúºÎ°ú Ïû¨Ïó∞Í≤∞ÎêòÏßÄ ÏïäÎäî Ïù¥Ïú†Ïù∏ Í≤ΩÏö∞ ÏÇ¨Ïö©ÏûêÏóêÍ≤å ÏïåÎ¶º
        if (reason === 'io server disconnect' || reason === 'io client disconnect') {
          this.showNotification('ÏÑúÎ≤ÑÏôÄÏùò Ïó∞Í≤∞Ïù¥ ÎÅäÍ≤ºÏäµÎãàÎã§. ÌéòÏù¥ÏßÄÎ•º ÏÉàÎ°úÍ≥†Ïπ®Ìï¥ Ï£ºÏÑ∏Ïöî.', 10000, true);
        }
      });
    },

    // Ìïë ÌÉÄÏù¥Î®∏ ÏãúÏûë (Ïó∞Í≤∞ Ïú†ÏßÄÎ•º ÏúÑÌïú Ï£ºÍ∏∞Ï†ÅÏù∏ Ìïë Ï†ÑÏÜ°)
    startPingTimer() {
      // Ïù¥ÎØ∏ Ïã§Ìñâ Ï§ëÏù∏ ÌÉÄÏù¥Î®∏Í∞Ä ÏûàÏúºÎ©¥ Ï†ïÎ¶¨
      if (this.pingInterval) {
        clearInterval(this.pingInterval);
      }

      // 20Ï¥àÎßàÎã§ ÏÑúÎ≤ÑÏóê Ìïë Ï†ÑÏÜ°
      this.pingInterval = setInterval(() => {
        if (this.socket && this.socket.connected) {
          console.log('ÏÑúÎ≤ÑÏóê Ìïë Ï†ÑÏÜ°...');
          this.socket.emit('ping', (response) => {
            if (response && response.status === 'ok') {
              console.log('ÏÑúÎ≤ÑÎ°úÎ∂ÄÌÑ∞ ÌêÅ ÏàòÏã†:', response.time);
              this.lastPongTime = response.time;
            }
          });
        } else {
          console.warn('ÏÜåÏºìÏù¥ Ïó∞Í≤∞ÎêòÏñ¥ ÏûàÏßÄ ÏïäÏïÑ ÌïëÏùÑ Ï†ÑÏÜ°Ìï† Ïàò ÏóÜÏäµÎãàÎã§.');

          // ÏÜåÏºìÏù¥ Ïó∞Í≤∞ÎêòÏñ¥ ÏûàÏßÄ ÏïäÏúºÎ©¥ ÌÉÄÏù¥Î®∏ Ï†ïÎ¶¨
          clearInterval(this.pingInterval);
          this.pingInterval = null;
        }
      }, 20000);
    }
  }
}
