<template>
  <div class="home">
    <div class="container">
      <h1 class="title">Quick Share</h1>
      
      <!-- Î∞© ÎßåÎì§Í∏∞ ÏòÅÏó≠ -->
      <div class="room-controls">
        <div class="input-group">
          <input
            v-model="roomCode"
            placeholder="Î∞© ÏΩîÎìú ÏûÖÎ†• (6ÏûêÎ¶¨)"
            maxlength="6"
            pattern="\d*"
            @input="validateInput"
          />
        </div>
        <div class="button-group">
          <button
            @click="createRoom"
            :disabled="roomCode && !isValidCode"
          >
            {{ roomCode ? 'ÏÉà Î∞© ÎßåÎì§Í∏∞' : 'ÏûêÎèô ÏΩîÎìúÎ°ú Î∞© ÎßåÎì§Í∏∞' }}
          </button>
        </div>
      </div>
      
      <!-- Î∞© Îì§Ïñ¥Í∞ÄÍ∏∞ ÏòÅÏó≠ -->
      <div class="room-join">
        <h2 class="section-title">Í∏∞Ï°¥ Î∞© Îì§Ïñ¥Í∞ÄÍ∏∞</h2>
        <div class="input-group">
          <input
            v-model="joinRoomCode"
            placeholder="Ï∞∏Ïó¨Ìï† Î∞© ÏΩîÎìú ÏûÖÎ†•"
            maxlength="6"
            pattern="\d*"
            @input="validateJoinInput"
          />
          <input
            v-model="joinUserName"
            placeholder="Ïù¥Î¶Ñ ÏûÖÎ†•"
            maxlength="20"
          />
        </div>
        <button
          @click="joinRoom"
          :disabled="!isValidJoinCode || !joinUserName"
        >
          Î∞© Îì§Ïñ¥Í∞ÄÍ∏∞
        </button>
      </div>
      
      <div v-if="error" class="error-message">
        {{ error }}
      </div>
      
      <div v-if="isLoading" class="loading-indicator">
        Ï≤òÎ¶¨ Ï§ë...
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

// axios Ïù∏Ïä§ÌÑ¥Ïä§ ÏÉùÏÑ±
const api = axios.create({
  baseURL: `https://file2.schoolworks.dev/api`,
  timeout: 5000,
  headers: {
    'Content-Type': 'application/json'
  }
});

// API ÏöîÏ≤≠/ÏùëÎãµ Î°úÍπÖÏùÑ ÏúÑÌïú Ïù∏ÌÑ∞ÏÖâÌÑ∞ Ï∂îÍ∞Ä
api.interceptors.request.use(request => {
  console.log('API ÏöîÏ≤≠:', request.method, request.url, request.data);
  return request;
});

api.interceptors.response.use(
  response => {
    console.log('API ÏùëÎãµ ÏÑ±Í≥µ:', response.status, response.data);
    return response;
  },
  error => {
    console.error('API ÏùëÎãµ Ïò§Î•ò:', 
      error.response ? `${error.response.status} ${JSON.stringify(error.response.data)}` : error.message
    );
    return Promise.reject(error);
  }
);

export default {
  name: 'HomeView',
  data() {
    return {
      roomCode: '',
      joinRoomCode: '',
      joinUserName: '',
      error: '',
      isLoading: false
    }
  },
  created() {
    // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóêÏÑú Ïù¥Ï†ÑÏóê ÏÇ¨Ïö©Ìïú Ïù¥Î¶Ñ Í∞ÄÏ†∏Ïò§Í∏∞
    const savedUserName = localStorage.getItem('userName');
    if (savedUserName) {
      this.joinUserName = savedUserName;
    }
  },
  computed: {
    isValidCode() {
      return /^\d{6}$/.test(this.roomCode);
    },
    isValidJoinCode() {
      return /^\d{6}$/.test(this.joinRoomCode);
    }
  },
  methods: {
    validateInput() {
      this.roomCode = this.roomCode.replace(/[^\d]/g, '');
    },
    validateJoinInput() {
      this.joinRoomCode = this.joinRoomCode.replace(/[^\d]/g, '');
    },
    async createRoom() {
      if (this.isLoading) return;
      this.error = '';
      this.isLoading = true;

      try {
        // ÏÉàÎ°úÏö¥ Î∞© ÏÉùÏÑ± (ÏÇ¨Ïö©ÏûêÍ∞Ä ÏßÄÏ†ïÌïú Î∞© ÏΩîÎìú ÎòêÎäî ÏûêÎèô ÏÉùÏÑ±)
        const requestData = {};
        
        // Î∞© ÏΩîÎìúÍ∞Ä Ïú†Ìö®Ìïú Í≤ΩÏö∞ ÏöîÏ≤≠Ïóê Ìè¨Ìï®
        if (this.roomCode && this.isValidCode) {
          requestData.roomCode = this.roomCode;
        }
        
        const response = await api.post('/rooms', requestData);
        
        if (response.data && response.data.roomCode) {
          this.saveRecentRoom(response.data.roomCode);
          this.navigateToRoom(response.data.roomCode);
        }
      } catch (error) {
        if (error.response && error.response.status === 409) {
          this.showError('Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî Î∞© ÏΩîÎìúÏûÖÎãàÎã§. Îã§Î•∏ ÏΩîÎìúÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
        } else if (error.response) {
          this.showError(error.response.data.message || 'ÏÑúÎ≤Ñ Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
        } else if (error.request) {
          this.showError('ÏÑúÎ≤ÑÏóê Ïó∞Í≤∞Ìï† Ïàò ÏóÜÏäµÎãàÎã§.');
        } else {
          this.showError('Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
        }
        console.error('Error:', error);
      } finally {
        this.isLoading = false;
      }
    },
    async joinRoom() {
      if (!this.isValidJoinCode) {
        this.showError('Ïú†Ìö®Ìïú Î∞© ÏΩîÎìúÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî (6ÏûêÎ¶¨ Ïà´Ïûê)');
        return;
      }
      
      if (!this.joinUserName.trim()) {
        this.showError('Ïù¥Î¶ÑÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî');
        return;
      }
      
      if (this.isLoading) return;
      this.error = '';
      this.isLoading = true;
      
      // Ïù¥Î¶Ñ Ï†ÄÏû•
      localStorage.setItem('userName', this.joinUserName);
      
      try {
        // Î∞±ÏóîÎìúÎ°ú Î£∏Î≤àÌò∏ÏôÄ Ïù¥Î¶ÑÏùÑ Ìï®Íªò Ï†ÑÏÜ°ÌïòÏó¨ Î∞© ÏûÖÏû• Ï≤òÎ¶¨
        const response = await api.post(`/rooms/${this.joinRoomCode}`, {
          userName: this.joinUserName
        });
        
        if (response.data) {
          this.saveRecentRoom(this.joinRoomCode);
          
          // Î£∏Î≤àÌò∏ÏôÄ ÏÇ¨Ïö©Ïûê Ïù¥Î¶ÑÏùÑ ÏøºÎ¶¨ ÌååÎùºÎØ∏ÌÑ∞Î°ú Ï†ÑÎã¨ÌïòÏó¨ Î∞©ÏúºÎ°ú Ïù¥Îèô
          this.$router.push({
            path: `/room/${this.joinRoomCode}`,
            query: { userName: this.joinUserName }
          });
        }
      } catch (error) {
        console.error('Î∞© Ï∞∏Ïó¨ Ïò§Î•ò:', error);
        
        if (error.response && error.response.status === 404) {
          this.showError('Ï°¥Ïû¨ÌïòÏßÄ ÏïäÎäî Î∞©ÏûÖÎãàÎã§.');
        } else if (error.response) {
          this.showError(error.response.data.message || 'ÏÑúÎ≤Ñ Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
        } else if (error.request) {
          this.showError('ÏÑúÎ≤ÑÏóê Ïó∞Í≤∞Ìï† Ïàò ÏóÜÏäµÎãàÎã§.');
        } else {
          this.showError('Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
        }
      } finally {
        this.isLoading = false;
      }
    },
    showError(message) {
      this.error = message;
    },
    saveRecentRoom(roomCode) {
      const recentRooms = JSON.parse(localStorage.getItem('recentRooms') || '[]');
      if (!recentRooms.includes(roomCode)) {
        recentRooms.unshift(roomCode);
        if (recentRooms.length > 5) recentRooms.pop();
        localStorage.setItem('recentRooms', JSON.stringify(recentRooms));
      }
    },
    navigateToRoom(roomCode) {
      // Î∞©ÏúºÎ°ú Ïù¥ÎèôÌï† Îïå ÏøºÎ¶¨ ÌååÎùºÎØ∏ÌÑ∞Î°ú ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ Ï†ÑÎã¨
      this.$router.push({
        path: `/room/${roomCode}`,
        query: this.joinUserName ? { userName: this.joinUserName } : undefined
      }).catch(err => {
        if (err.name !== 'NavigationDuplicated') {
          throw err;
        }
      });
    }
  }
}
</script>

